import React from 'react';
import { connect } from 'react-redux';
import * as encuestaActions from '../../actions/encuestaActions';

class TextoOpcion extends React.Component {

    handleChange = (ev) => {
        const flag = ev.target.checked;
        if (this.props.multiple === 1) {
            this.props.agregar_respuesta_uno(this.props.pregunta, this.props.opcion);
            return ;
        }
        
        if (flag) {
            this.props.agregar_respuesta(this.props.pregunta, this.props.opcion);
        } else {
            this.props.quitar_respuesta(this.props.pregunta, this.props.opcion);
        }
    }

    render() {
        const { opcion } = this.props;
        let type = 'checkbox';
        if (this.props.multiple === 1) {
            type = 'radio';
        }

        return (
            <div className="form-check">
                <input
                    className="form-check-input"
                    type={type} 
                    name={this.props.pregunta}
                    id={opcion.id}
                    value={opcion.id}
                    onChange={this.handleChange}
                />
                <label className="form-check-label" htmlFor={opcion.id}>
                    {opcion.titulo}
                </label>
            </div>
        );
    }
}

export default connect(null, encuestaActions)(TextoOpcion);
