import React, { Fragment } from 'react';
import ImagenOpciones from './ImagenOpciones';
import TextoOpcion from './TextoOpcion';
import { connect } from 'react-redux';
import * as encuestaActions from '../../actions/encuestaActions';

class Opciones extends React.Component {

    handleMultiples = (opciones) => {
        const file = opciones.filter(op => op.imagen !== null);

        if (file.length) {
            return (
                <div className="row">
                    {
                        opciones.map((opcion, index) => (
                            <ImagenOpciones
                                key={opcion.id}
                                opcion={opcion}
                                pregunta={this.props.pregunta}
                                index={index}
                            />
                        ))
                    }
                </div>
            )
        } else {
            const especifica = this.props.encuesta.preguntas[this.props.pregunta].especifica;
            const respuesta = this.props.encuesta.preguntas[this.props.pregunta].respuestas[0];
            let flag = false;
            if (respuesta) {
                flag = respuesta.especifica;
            }

            return (
                <Fragment>
                    {this.props.encuesta.preguntas[this.props.pregunta].expecifica}
                    {
                        opciones.map((opcion) => (
                            <TextoOpcion
                                key={opcion.id}
                                opcion={opcion}
                                pregunta={this.props.pregunta}
                                multiple={this.props.encuesta.preguntas[this.props.pregunta].max_respuestas}
                            />
                        ))
                    }
                    {
                        especifica && flag
                        ?
                            <div className="form-group">
                                <textarea
                                    className="form-control"
                                    name="abierto[]"
                                    onChange={(ev) => this.props.agregar_respuesta_texto(this.props.pregunta, ev.target.value)}
                                ></textarea>
                            </div>
                        :   ''
                    }
                </Fragment>
            )
                    
        }
        
    }

    handleMultiplesDependencia = () => {
        const dependencia = this.props.encuesta.preguntas[this.props.pregunta].dependencia;
        const index = this.props.encuesta.preguntas.findIndex(pre => pre.id === dependencia);
        const opciones = this.props.encuesta.preguntas[index].respuestas;
        console.log(opciones);
        return (
            <div className="row">
                {
                    opciones.map((opcion, index) => (
                        <ImagenOpciones
                            key={opcion.id}
                            opcion={opcion}
                            pregunta={this.props.pregunta}
                            index={index}
                        />
                    ))
                }
            </div>
        )
    }

    render() {

        // return this.ponerAbiertoOpcion();
        const {pregunta} = this.props;
        if (this.props.encuesta.preguntas[pregunta].respuestas_multiples) {
            return this.handleMultiples(this.props.encuesta.preguntas[pregunta].opciones);
        } else if (this.props.encuesta.preguntas[pregunta].dependencia) {
            return this.handleMultiplesDependencia();
        } else {
            return (
                <div className="form-group">
                    <textarea
                        className="form-control"
                        name="abierto[]"
                        onChange={(ev) => this.props.agregar_respuesta_texto(pregunta, ev.target.value)}
                    ></textarea>
                </div>
            )
        }
    }
    
}

const mapStateToProps = (reducers) => reducers.encuestaReducers;

export default connect(mapStateToProps, encuestaActions)(Opciones);
