import {
    CARGANDO,
    ERROR,
    TRAER_UNO,
    DATOS_ENCUESTADO,
    GUARDADO
} from '../types/encuestaTypes';

const INITIA_STATE = {
    encuesta: {},
    datos_encuestado: {},
    cargando: true,
    mensaje: '',
    error: ''
};

export default (state = INITIA_STATE, action) => {
    switch (action.type) {
        case TRAER_UNO:
            return {
                ...state,
                encuesta: action.payload,
                cargando: false,
                error: ''
            };
        case CARGANDO:
            return {
                ...state,
                cargando: true
            };
        case ERROR:
            return {
                ...state,
                cargando: false,
                error: action.payload
            };
        case DATOS_ENCUESTADO:
            return {
                ...state,
                datos_encuestado: action.payload
            };
        case GUARDADO:
            return {
                ...state,
                encuesta: {},
                cargando: false,
                mensaje: action.payload
            };
        default:
            return state
    }
}