import React from 'react';
import { connect } from 'react-redux';
import * as encuestaActions from '../../actions/encuestaActions';

class ImagenOpciones extends React.Component{
    state = {
        checked: false,
    };

    handleClick = () => {

        if (!this.state.checked) {
            this.props.agregar_respuesta(this.props.pregunta, this.props.opcion);
        } else {
            this.props.quitar_respuesta(this.props.pregunta, this.props.opcion);
        }

        this.setState({
            checked: !this.state.checked
        });
    }

    render() {
        const {opcion} = this.props;
        let classButton = 'fa fa-check hidden';
        if (this.state.checked) {
            classButton += 'fa fa-check checked-image';
        }
        return (   
            <div className="col-xs-4 col-sm-3 col-md-3 text-center">
                <button type="button" onClick={this.handleClick} className="image-check">
                    <img className="img-responsive img-op" src={ opcion.imagen } alt={opcion.imagen}/>
                    <i className={classButton}></i>
                </button>
            </div>
        )
    }
};

export default connect(null, encuestaActions)(ImagenOpciones);
