import React from 'react';
import { connect } from 'react-redux';
import * as encuestaActions from '../../actions/encuestaActions';
import Loader from '../utils/Loader';
import DatosEncuesta from '../DatosEncuestado';
import Guardado from './Guardado';

import Opciones from '../Encuestas/Opciones'
class Encuesta extends React.Component {
    state = {
        nombres: '',
        apellidos: '',
        edad: 0,
        genero: 'M',
        ciudad: '',
        email: '',
        tiene_hijos: false,
        edad_hijos: ''
    }

    async componentDidMount() {
        await this.props.traer_uno(this.props.match.params.slug);
    }

    handleSumit = (ev) => {
        ev.preventDefault();
        this.props.guardar_datos(this.state);
    }

    handleChangeInput = (ev) => {
        const name = ev.target.id;
        const value = ev.target.value;
        this.setState({
            [name]: value
        });
    }

    render() {
        console.log(this.props);
        if (this.props.cargando) {
            return <Loader />
        }

        if (this.props.mensaje) {
            return <Guardado mensaje={this.props.mensaje} />
        }

        return (
            <form className="form-encuesta" onSubmit={this.handleSumit}>
                <h4 className="text-center">{this.props.encuesta.nombre}</h4>
                <br />
                <DatosEncuesta  onChange={this.handleChangeInput} {...this.state}/>
                <br />
                {
                    this.props.encuesta.preguntas.map((pregunta, index) => (
                        <div key={pregunta.id} className="content-preguntas">
                            <h5>{ pregunta.nombre }</h5>
                            <p className="text-muted">{ pregunta.descripcion }</p>
                            <Opciones pregunta={index}/>
                        </div>
                    ))
                }
                <div className="text-center">
                    <button type="submit" className="btn btn-primary">Enviar informaci√≥n</button>
                </div>
            </form>
        )
    }
}

const mapStateToProps = (reducers) => reducers.encuestaReducers;

export default connect(mapStateToProps, encuestaActions)(Encuesta);
