export const guardar_datos = (datos_encuestado) => async (dispatch, getState) => {
    const { encuesta } = getState().encuestaReducers;
    
    const respuestas = [];

    encuesta.preguntas.forEach(pregunta => {
        if (pregunta.respuestas.length > 0) {
            pregunta.respuestas.forEach((res)=> {
                respuestas.push({
                    'id_pregunta': pregunta.id,
                    'id_opcion': res.id,
                    'texto_respuesta': pregunta.respuesta_texto,
                    'relacion_pregunta': pregunta.dependencia || 0
                });
            });
        } else {
            respuestas.push({
                'id_pregunta': pregunta.id,
                'id_opcion': 0,
                'texto_respuesta': pregunta.respuesta_texto,
                'relacion_pregunta': pregunta.dependencia || 0
            });
        }
        
    });

    const data_send = {
        id_encuesta: encuesta.slug,
        respuestas,
        datos_encuestado: datos_encuestado,
    };

    try {
        await axios.post('http://www.mktestudios.com/api/v1/respuestas/', data_send);
        dispatch({
            type: GUARDADO,
            payload: 'El formulario ha sido guardado correctamente muchas gracias.'
        });
    } catch (error) {
        console.log(error);
    }
    
}
